.projectsBlock {
    background-color: #9ac59a;
}

.projectsContainer {
    flex-direction: column;
    justify-content: flex-start;
}

.title {
    background-color: #af9ec2;
}

.projects {
    border: 1px solid;
    width: 100%;
    display: flex;
    justify-content: space-between;
    flex-wrap: wrap;
}

@media (max-width: 1130px) {
    .projects {
        justify-content: space-around;
    }

}